"use client";

import { motion } from "framer-motion";
import { Sidebar } from "@/components/layout/Sidebar";
import { Header } from "@/components/layout/Header";
import { PageTransition, StaggerContainer, StaggerItem } from "@/components/layout/PageTransition";
import { KPIGrid } from "@/components/dashboard/KPIGrid";
import { AlertsFeed } from "@/components/dashboard/AlertsFeed";
import { GlassCard } from "@/components/ui/GlassCard";
import { DonutChart } from "@/components/charts/AnimatedPieChart";
import { AnimatedBarChart } from "@/components/charts/AnimatedBarChart";
import { SegmentMatrix } from "@/components/charts/SegmentMatrix";
import { CardSkeleton, ChartSkeleton } from "@/components/ui/LoadingSkeleton";
import { useDashboardSummary, useAlerts } from "@/hooks/useAPI";
import { RefreshCw } from "lucide-react";

export default function DashboardPage() {
  const { data: summary, isLoading: summaryLoading, refetch } = useDashboardSummary();
  const { data: alerts, isLoading: alertsLoading } = useAlerts({ active_only: true });

  const isLoading = summaryLoading || alertsLoading;

  // Transform data for charts
  const shiftDistributionData = summary?.shifts_by_direction
    ? Object.entries(summary.shifts_by_direction).map(([name, value]) => ({
        name: name.charAt(0) + name.slice(1).toLowerCase(),
        value,
        color:
          name === "INCREASE"
            ? "#10b981"
            : name === "DECREASE"
            ? "#f43f5e"
            : "#6b7280",
      }))
    : [];

  const nonMovingData = summary?.non_moving_by_category
    ? Object.entries(summary.non_moving_by_category).map(([name, value]) => ({
        name: name.replace("_", " "),
        value,
        color:
          name === "DEAD_STOCK"
            ? "#f43f5e"
            : name === "NON_MOVING"
            ? "#f59e0b"
            : name === "SLOW_MOVING"
            ? "#06b6d4"
            : "#10b981",
      }))
    : [];

  const segmentData = summary?.segmentation_summary
    ? Object.entries(summary.segmentation_summary).map(([segment, count]) => ({
        segment,
        count,
        percentage:
          (count /
            Object.values(summary.segmentation_summary).reduce((a, b) => a + b, 0)) *
          100,
      }))
    : [];

  const alertsByPriorityData = summary?.alerts_by_priority
    ? Object.entries(summary.alerts_by_priority).map(([name, value]) => ({
        name: name.replace("_", " "),
        value,
        color:
          name === "P1_CRITICAL"
            ? "#f43f5e"
            : name === "P2_HIGH"
            ? "#f59e0b"
            : name === "P3_MEDIUM"
            ? "#06b6d4"
            : name === "P4_LOW"
            ? "#8b5cf6"
            : "#10b981",
      }))
    : [];

  return (
    <div className="flex min-h-screen">
      <Sidebar />

      <main className="flex-1 ml-64 p-6">
        <Header
          title="Dashboard"
          subtitle={
            summary?.analysis_date
              ? `Last analysis: ${new Date(summary.analysis_date).toLocaleString()}`
              : "Overview of inventory health"
          }
          onRefresh={refetch}
          isRefreshing={isLoading}
        />

        <PageTransition>
          {/* KPI Grid */}
          <div className="mb-6">
            {isLoading ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-4">
                {[...Array(5)].map((_, i) => (
                  <CardSkeleton key={i} />
                ))}
              </div>
            ) : (
              <KPIGrid
                totalSkus={summary?.total_skus || 0}
                totalLocations={summary?.total_locations || 0}
                demandShiftsCount={summary?.demand_shifts_count || 0}
                nonMovingCount={summary?.non_moving_count || 0}
                criticalAlerts={summary?.critical_alerts || 0}
              />
            )}
          </div>

          <StaggerContainer className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
            {/* Demand Shift Distribution */}
            <StaggerItem>
              {isLoading ? (
                <ChartSkeleton height={350} />
              ) : (
                <DonutChart
                  data={shiftDistributionData}
                  title="Demand Shift Distribution"
                  centerLabel="Total Shifts"
                  centerValue={summary?.demand_shifts_count}
                  height={280}
                />
              )}
            </StaggerItem>

            {/* Non-Moving by Category */}
            <StaggerItem>
              {isLoading ? (
                <ChartSkeleton height={350} />
              ) : (
                <DonutChart
                  data={nonMovingData}
                  title="Non-Moving Inventory"
                  centerLabel="Total Items"
                  centerValue={summary?.non_moving_count}
                  height={280}
                />
              )}
            </StaggerItem>

            {/* Alerts by Priority */}
            <StaggerItem>
              {isLoading ? (
                <ChartSkeleton height={350} />
              ) : (
                <AnimatedBarChart
                  data={alertsByPriorityData}
                  title="Alerts by Priority"
                  height={280}
                  horizontal
                />
              )}
            </StaggerItem>
          </StaggerContainer>

          <div className="grid grid-cols-1 xl:grid-cols-2 gap-6 mt-6">
            {/* Segmentation Matrix */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.4 }}
            >
              {isLoading ? (
                <ChartSkeleton height={400} />
              ) : (
                <SegmentMatrix data={segmentData} title="ABC-XYZ Segmentation Matrix" />
              )}
            </motion.div>

            {/* Recent Alerts */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.5 }}
            >
              {alertsLoading ? (
                <ChartSkeleton height={400} />
              ) : (
                <AlertsFeed alerts={alerts || []} maxItems={6} title="Recent Alerts" />
              )}
            </motion.div>
          </div>
        </PageTransition>
      </main>
    </div>
  );
}
